import React from 'react'
import { slide as Menu } from 'react-burger-menu'
import { Link } from 'gatsby'
import { Location } from '@reach/router'
const SideBar = props => {
  const links = [
    {
      path: '/',
      name: 'home',
    },
    {
      path: '/projects',
      name: 'projects',
    },
    {
      path: '/resume',
      name: 'resume',
    },
  ]

  return (
    <Menu {...props}>
      {links.map((l, i) => {
        const url = `${props.location}`

        const check =
          url === 'https://davidnymanmusic.github.io/portfolio/' ? true : false

        return (
          <Link key={i} to={`${l.path}`}>
            {url.includes(l.name === 'home' ? check : l.name)
              ? `> ${l.name} `
              : `${l.name} `}
            {url.includes(l.name === 'home' ? check : l.name) ? (
              <span className="blinking-cursor">|</span>
            ) : null}
          </Link>
        )
      })}
    </Menu>
  )
}
export default () => {
  return (
    <Location>
      {locationProps => {
        return (
          <SideBar
            location={
              'https://davidnymanmusic.github.io/portfolio' +
              locationProps.location.pathname
            }
          />
        )
      }}
    </Location>
  )
}
